import react , { useState } from 'react'; 

function Movie(){
    let posterNumber = [1,2,3,4,5,6];
    let [sliderPosition, setSliderPosition] = useState(0);
    return (
        <main>
            <div class="slider">
                <button className='slider-prev' onClick={()=>{ if(sliderPosition != 0) setSliderPosition(sliderPosition - 36)  }}></button>
                <button className='slider-next' onClick={()=>{ if(sliderPosition < 36*5 ) setSliderPosition(sliderPosition + 36) }}></button>
                <div className='slider-box' style={ { transform: 'translate(-'+sliderPosition+'rem, 0)' } }>
                {
                    posterNumber.map( (e, index) => { return <MoovieCard posterNumber={index+1}/> } )
                }
                </div>
            </div>
        </main>
    )
}

function MoovieCard(props){
    return(
      <>
        <div className='card'>
          <img src={'/img/poster' + props.posterNumber + '.jpg'}></img>
          <h3>제목{props.posterNumber}</h3>
        </div>
      </>
    )
  }

export default Movie;


